/*
  Post-specific style
*/

@mixin btn-sharing-color($light-color, $important: false) {
  @if $important {
    color: var(--btn-share-color, $light-color) !important;
  } @else {
    color: var(--btn-share-color, $light-color);
  }
}

@mixin btn-post-nav {
  width: 50%;
  position: relative;
  border-color: var(--btn-border-color);
}

@mixin dot($pl: 0.25rem, $pr: 0.25rem) {
  content: "\2022";
  padding-left: $pl;
  padding-right: $pr;
}

%text-color {
  color: var(--text-color);
}

/* 포스트 본문 텍스트 색상 강제 적용 */
.post-content {
  color: #424245 !important;
  
  p, li, span, div {
    color: #424245 !important;
  }
  
  /* 링크는 보라색 유지 */
  a {
    color: rgb(153, 134, 179) !important;
    
    &:hover, &:active, &:focus {
      color: #ffffff !important;
      background: rgba(153, 134, 179, 0.2);
      border-radius: 4px;
      padding: 2px 4px;
      transition: all 0.2s ease;
    }
  }
  
  /* 제목은 진한 회색 유지 */
  h1, h2, h3, h4, h5, h6 {
    color: #424245 !important;
  }
}

h1 + .post-meta {
  span + span::before {
    @include dot;
  }

  em {
    color: rgb(153, 134, 179) !important;

    a {
      color: rgb(153, 134, 179) !important;
      
      &:hover, &:active, &:focus {
        color: #ffffff !important;
        background: rgba(153, 134, 179, 0.2);
        border-radius: 4px;
        padding: 2px 4px;
        transition: all 0.2s ease;
      }
    }
  }
  
  span {
    color: rgb(153, 134, 179) !important;
    
    a {
      color: rgb(153, 134, 179) !important;
      
      &:hover, &:active, &:focus {
        color: #ffffff !important;
        background: rgba(153, 134, 179, 0.2);
        border-radius: 4px;
        padding: 2px 4px;
        transition: all 0.2s ease;
      }
    }
  }
  
  /* 모든 메타데이터 텍스트 통일 */
  * {
    color: rgb(153, 134, 179) !important;
    
    a {
      color: rgb(153, 134, 179) !important;
      
      &:hover, &:active, &:focus {
        color: #ffffff !important;
        background: rgba(153, 134, 179, 0.2);
        border-radius: 4px;
        padding: 2px 4px;
        transition: all 0.2s ease;
      }
    }
  }
}

img.preview-img {
  margin: 0;
  border-radius: 6px;

  &.bg[data-loaded=true] {
    background: var(--preview-img-bg);
  }
}

.post-tail-wrapper {
  margin-top: 6rem;
  border-bottom: 1px double var(--main-border-color);
  font-size: 0.85rem;

  .post-meta a:not(:hover) {
    @extend %link-underline;
  }
}

.post-tags {
  line-height: 2rem;
}

.post-navigation {
  padding-top: 3rem;
  padding-bottom: 4rem;

  .btn {
    @include btn-post-nav;

    color: var(--link-color);

    &:hover {
      background: rgb(203, 171, 237);
      color: #fff;
      border-color: rgb(203, 171, 237);
    }

    &.disabled {
      @include btn-post-nav;

      pointer-events: auto;
      cursor: not-allowed;
      background: none;
      color: gray;

      &:hover {
        border-color: none;
      }
    }

    &.btn-outline-primary.disabled:focus {
      box-shadow: none;
    }

    &::before {
      color: var(--text-muted-color);
      font-size: 0.65rem;
      text-transform: uppercase;
      content: attr(prompt);
    }

    &:first-child {
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
      left: 0.5px;
    }

    &:last-child {
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
      right: 0.5px;
    }
  }

  p {
    font-size: 1.1rem;
    line-height: 1.5rem;
    margin-top: 0.3rem;
    white-space: normal;
  }

} /* .post-navigation */

@keyframes fade-up {
  from {
    opacity: 0;
    position: relative;
    top: 2rem;
  }
  to {
    opacity: 1;
    position: relative;
    top: 0;
  }
}

#toc-wrapper {
  border: none;
  position: -webkit-sticky;
  position: sticky;
  top: 80px !important; /* topbar 높이 + 여백 */
  max-height: calc(100vh - 100px) !important;
  overflow-y: auto !important;
  z-index: 10 !important;
  transition: top 0.2s ease-in-out;
  animation: fade-up 0.8s;
  background: transparent;
  backdrop-filter: none;
  -webkit-backdrop-filter: none;
  border-radius: 0;
  padding: 0;
  box-shadow: none;
}

#toc li a {
  font-size: 0.8rem;
  color: #1d1d1f;
  text-decoration: none;
  padding: 0.5rem 0;
  border-radius: 0;
  transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  position: relative;
  overflow: hidden;
  background: transparent;
  border: none;
  backdrop-filter: none;
  -webkit-backdrop-filter: none;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(66, 66, 69, 0.1), transparent);
    transition: left 0.5s ease;
  }

  &:hover::before {
    left: 100%;
  }

  &:hover {
    background: transparent;
    transform: translateX(4px);
    box-shadow: none;
    backdrop-filter: none;
    -webkit-backdrop-filter: none;
    border-color: transparent;
    color: #1d1d1f;
    font-weight: 600;
  }

  &.nav-link:not(.active) {
    color: #1d1d1f;
  }

  &.nav-link.active {
    background: transparent;
    color: #1d1d1f;
    font-weight: 700;
    box-shadow: none;
    border: none;
    backdrop-filter: none;
    -webkit-backdrop-filter: none;
    position: relative;
    
    &::after {
      display: none !important;
    }
  }
  
  /* Bootstrap TOC 기본 활성화 스타일 복원 */
  &.active {
    background: transparent;
    color: #1d1d1f;
    font-weight: 700;
    box-shadow: none;
    border: none;
    backdrop-filter: none;
    -webkit-backdrop-filter: none;
    position: relative;
    
    &::after {
      display: none !important;
    }
  }

}

nav[data-toggle=toc] {
  /* 동적 높이 조정 - 필요시에만 스크롤 */
  
  /* 스크롤바 투명하게 만들기 (동적으로 스크롤이 필요할 때) */
  &::-webkit-scrollbar {
    width: 6px !important;
    background: transparent !important;
  }
  
  &::-webkit-scrollbar-track {
    background: transparent !important;
  }
  
  &::-webkit-scrollbar-thumb {
    background: transparent !important;
    border-radius: 3px !important;
    
    &:hover {
      background: rgba(128, 102, 153, 0.2) !important;
    }
  }
  
  /* Firefox에서 스크롤바 투명하게 */
  scrollbar-width: thin !important;
  scrollbar-color: transparent transparent !important;
  
  /* IE/Edge에서 스크롤바 투명하게 */
  -ms-overflow-style: auto !important;
  
  .nav {
    .nav > li > a.active {
      font-weight: 600 !important;
    }
  }
  
  /* TOC 버튼을 오른쪽으로 20px 이동 (5px + 10px + 5px) */
  .nav {
    margin-left: 10px !important;
  }
  
  /* TOC 버튼 간격 통일 및 heading 레벨별 들여쓰기 */
  .nav-link {
    margin-bottom: 0.5rem !important; /* 버튼 간 위아래 간격 통일 */
    white-space: nowrap !important; /* 텍스트 줄바꿈 방지 */
    overflow: hidden !important; /* 오버플로우 숨김 */
    text-overflow: ellipsis !important; /* ... 처리 */
    
    /* H1 항목 (최상위 레벨) */
    &.toc-h1 {
      padding-left: 0 !important;
      font-weight: 600 !important;
    }
    
    /* H2 항목 (하위 레벨) - 30px 들여쓰기 */
    &.toc-h2 {
      padding-left: 30px !important;
    }
  }
  
  /* 반응형 TOC 스타일 - 점진적 변화 */
  
  /* 태블릿 크기 (1024px 이하) */
  @media (max-width: 1024px) {
    .nav {
      margin-left: 8px !important; /* 약간 줄임 */
    }
    
    .nav-link {
      &.toc-h2 {
        padding-left: 25px !important; /* 약간 줄임 */
      }
    }
  }
  
  /* 중간 태블릿 크기 (768px 이하) */
  @media (max-width: 768px) {
    .nav {
      margin-left: 5px !important; /* 더 줄임 */
    }
    
    .nav-link {
      font-size: 0.9rem !important; /* 약간 줄임 */
      padding: 0.45rem 0.9rem !important; /* 약간 줄임 */
      
      &.toc-h2 {
        padding-left: 20px !important; /* 더 줄임 */
      }
    }
  }
  
  /* 작은 태블릿/큰 모바일 (640px 이하) */
  @media (max-width: 640px) {
    .nav {
      margin-left: 2px !important; /* 거의 중앙 */
    }
    
    .nav-link {
      font-size: 0.85rem !important; /* 더 줄임 */
      padding: 0.4rem 0.8rem !important; /* 더 줄임 */
      
      &.toc-h2 {
        padding-left: 18px !important; /* 더 줄임 */
      }
    }
  }
  
  /* 모바일 크기 (480px 이하) */
  @media (max-width: 480px) {
    .nav {
      margin-left: 0 !important; /* 중앙 정렬 */
    }
    
    .nav-link {
      font-size: 0.8rem !important; /* 모바일 폰트 */
      padding: 0.35rem 0.7rem !important; /* 모바일 패딩 */
      margin-bottom: 0.4rem !important; /* 간격 약간 줄임 */
      
      &.toc-h2 {
        padding-left: 15px !important; /* 모바일 들여쓰기 */
      }
    }
  }
  
  /* 작은 모바일 크기 (360px 이하) */
  @media (max-width: 360px) {
    .nav {
      margin-left: -2px !important; /* 약간 왼쪽 */
    }
    
    .nav-link {
      font-size: 0.75rem !important; /* 작은 폰트 */
      padding: 0.3rem 0.6rem !important; /* 작은 패딩 */
      margin-bottom: 0.3rem !important; /* 간격 줄임 */
      
      &.toc-h2 {
        padding-left: 12px !important; /* 작은 들여쓰기 */
      }
    }
  }
}

/* --- Related Posts --- */

#related-posts {
  > h3 {
    @include label(1.1rem, 600);
  }
  
  /* 강제로 투명한 배경 적용 */
  .card, .post-card, .related-card {
    background: transparent !important;
    background-color: transparent !important;
    backdrop-filter: blur(2px) saturate(1.01) !important;
    -webkit-backdrop-filter: blur(2px) saturate(1.01) !important;
    box-shadow: 
      0 1px 4px rgba(128, 102, 153, 0.01),
      0 1px 2px rgba(255, 255, 255, 0.01),
      inset 0 1px 0 rgba(255, 255, 255, 0.02),
      inset 0 -1px 0 rgba(128, 102, 153, 0.002) !important;
    border: 1px solid rgba(255, 255, 255, 0.03) !important;
  }

  em {
    @extend %normal-font-style;

    color: var(--relate-post-date);
  }

  .card {
    border: none !important;
    /* 버튼과 동일한 그라데이션 세기 */
    background: 
      radial-gradient(ellipse at 20% 10%, rgba(255, 255, 255, 0.6) 0%, transparent 50%),
      radial-gradient(ellipse at 80% 90%, rgba(196, 190, 226, 0.3) 0%, transparent 50%),
      linear-gradient(135deg, rgba(255, 255, 255, 0.15) 0%, rgba(196, 190, 226, 0.1) 100%) !important;
    box-shadow: 
      0 2px 8px rgba(128, 102, 153, 0.02),
      0 1px 4px rgba(255, 255, 255, 0.02),
      inset 0 1px 0 rgba(255, 255, 255, 0.03),
      inset 0 -1px 0 rgba(128, 102, 153, 0.005) !important;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    backdrop-filter: blur(4px) saturate(1.01) !important;
    -webkit-backdrop-filter: blur(4px) saturate(1.01) !important;
    border-radius: 16px;
    padding: 1rem;
    border: 1px solid rgba(255, 255, 255, 0.05) !important;

    h3 {
      @extend %text-color;
      color: #424245 !important; /* 텍스트 가독성 향상 */
    }

    a {
      color: rgb(153, 134, 179) !important;
      text-decoration: none !important;
      border-bottom: none !important;
    }

    a:hover {
      color: #ffffff !important;
      background: rgba(153, 134, 179, 0.2);
      border-radius: 4px;
      padding: 2px 4px;
      text-decoration: none !important;
      border-bottom: none !important;
    }

    &:hover {
      -webkit-transform: translate3d(0, -3px, 0);
      transform: translate3d(0, -3px, 0);
      backdrop-filter: blur(12px) saturate(1.05) brightness(1.01);
      -webkit-backdrop-filter: blur(12px) saturate(1.05) brightness(1.01);
      background: 
        radial-gradient(ellipse at 20% 10%, rgba(255, 255, 255, 0.05) 0%, transparent 70%),
        radial-gradient(ellipse at 80% 90%, rgba(196, 190, 226, 0.03) 0%, transparent 70%),
        linear-gradient(135deg, rgba(255, 255, 255, 0.02) 0%, rgba(196, 190, 226, 0.01) 100%) !important;
      box-shadow: 
        0 8px 24px rgba(128, 102, 153, 0.06),
        0 4px 12px rgba(255, 255, 255, 0.08),
        inset 0 2px 0 rgba(255, 255, 255, 0.1),
        inset 0 -2px 0 rgba(128, 102, 153, 0.02);
      border: 1px solid rgba(255, 255, 255, 0.12);
    }
  }

  p {
    font-size: 0.9rem;
    margin-bottom: 0.5rem;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    line-clamp: 2; /* 표준 속성 추가 */
    -webkit-box-orient: vertical;
    color: #424245 !important; /* 텍스트 가독성 향상 */
  }

  a:hover {
    text-decoration: none;
  }

  ul {
    list-style-type: none;
    padding-inline-start: 1.5rem;

    > li::before {
      background: #c2c9d4;
      width: 5px;
      height: 5px;
      border-radius: 1px;
      display: block;
      content: "";
      position: relative;
      top: 1rem;
      right: 1rem;
    }
  }
}

#tail-wrapper {
  min-height: 2rem;

  > div:last-of-type {
    margin-bottom: 2rem;
  }

  #disqus_thread {
    min-height: 8.5rem;
  }

}

.post-tail-bottom a {
  color: inherit;
}

%btn-share-hovor {
  color: var(--btn-share-hover-color) !important;
}

.share-wrapper {
  vertical-align: middle;
  user-select: none;

  .share-icons {
    font-size: 1.2rem;

    a {
      &:not(:last-child) {
        margin-right: 0.25rem;
      }

      &:hover {
        text-decoration: none;

        > i {
          @extend %btn-share-hovor;
        }
      }
    }

    > i {
      position: relative;
      bottom: 1px;

      @extend %cursor-pointer;

      &:hover {
        @extend %btn-share-hovor;
      }
    }

    .fab {
      &.fa-twitter {
        @include btn-sharing-color(rgba(29, 161, 242, 1));
      }

      &.fa-facebook-square {
        @include btn-sharing-color(rgb(66, 95, 156));
      }

      &.fa-telegram {
        @include btn-sharing-color(rgb(39, 159, 217));
      }

      &.fa-weibo {
        @include btn-sharing-color(rgb(229, 20, 43));
      }
    }

  } /* .share-icons */

  .fas.fa-link {
    @include btn-sharing-color(rgb(171, 171, 171));
  }

} /* .share-wrapper */

.share-label {
  @include label(inherit, 400, inherit);

  &::after {
    content: ":";
  }
}

.license-wrapper {
  line-height: 1.2rem;

  > a {
    color: var(--text-color);

    &:hover {
      @extend %link-hover;
    }
  }

  span:last-child {
    font-size: 0.85rem;
  }

} /* .license-wrapper */

@media all and (max-width: 576px) {
  .preview-img[data-src] {
    margin-top: 2.2rem;
  }

  .post-tail-bottom {
    -ms-flex-wrap: wrap-reverse !important;
    flex-wrap: wrap-reverse !important;

    > div:first-child {
      width: 100%;
      margin-top: 1rem;
    }
  }
}

@media all and (max-width: 768px) {
  .post-content > p > img {
    max-width: calc(100% + 1rem);
  }
}

/* Hide SideBar and TOC */
@media all and (max-width: 849px) {
  .post-navigation {
    padding-left: 0;
    padding-right: 0;
    margin-left: -0.5rem;
    margin-right: -0.5rem;
  }

  .preview-img[data-src] {
    max-width: 100vw;
    border-radius: 0;
  }
}

/* 다크 모드 대응 - Apple Liquid Glass */
@media (prefers-color-scheme: dark) {
  #toc-wrapper {
    background: transparent;
    border: none;
  }
  
  #toc li a {
    background: transparent;
    border: none;
    color: #1d1d1f;
  }
  
  #toc li a:hover {
    background: transparent;
    border: none;
    box-shadow: none;
    color: #1d1d1f;
    font-weight: 600;
  }
  
  #toc li a.nav-link:not(.active) {
    color: #1d1d1f;
  }
  
  #toc li a.nav-link.active {
    background: transparent;
    color: #1d1d1f;
    border: none;
    box-shadow: none;
    font-weight: 700;
    position: relative;
    
    &::after {
      display: none !important;
    }
  }
  
  /* Bootstrap TOC 기본 활성화 스타일 복원 - 다크 모드 */
  #toc li a.active {
    background: transparent;
    color: #1d1d1f;
    border: none;
    box-shadow: none;
    font-weight: 700;
    position: relative;
    
    &::after {
      display: none !important;
    }
  }
}

